import Scorll from "../../component/scorll.vue"
export default{
	data(){
		return {
			arr:[]
		}
	},
	methods:{
		pulldown(e){//刷新
			let {stop}=e;
			setTimeout(()=>{
				stop()//结束
			},500)
		},
		reachbottom(e){//加载
			let {stop}=e;
			setTimeout(()=>{
				stop()
			},1000)
		},
	},
	created() {
		let arr=[]
		for(let i=0;i<50;i++){
			arr.push(i)
		};
		this.arr=arr;
